import { Component } from 'react';
//import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
//import administrador from '../src/assets/img/images01.jpg';
import './App.css';
// import Link from './link';
import Cajas from './props/Cajas';
//import Add from './addperson/Add';
import ModalAddPerson from './addperson/Modaladdperson';
import EditPerson from './editPerson/editPerson';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content';
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';
import Header1 from './header/header1';
import Header2 from './header/header2';



//import Boton from './boton/boton';
const MySwal = withReactContent(Swal);

class App extends Component {

  state = {
    urlPersons: "http://localhost:8080/api/usuario/",
    personas: [],
    show: false,
    editshow: false,
    dataEdit: [],
    loadingState: false
  }

  consumoApi = () => {

  }

  componentDidMount() {
    console.log("Componente MOntado ");

      window.fetch("http://localhost:8080/api/usuario/")
        .then((response) => {
          return response.json()
        })
        .then(data => {
          console.log(data);

          this.setState({ personas: data});


        }, 9000)


  }


  changeModalState = () => {
    this.setState({show:true})
  }

  closeModalState = () => {
    this.setState({show:false})
  }

  closeEditModal = () => {
    this.setState({ editshow: false})
  }

  addPerson = (nombre, edad,id, imagen) => {



    let dataBack = {
      id: id,
      nombre: nombre,
      edad: edad,
      imagen: imagen
    }

    let arrPersons = this.state.personas;

    arrPersons.push(dataBack);
    this.setState({ loadingState:true});
    fetch("http://localhost:8080/api/usuario/", {
      method: 'POST',
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(dataBack)

    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        if (data.status == "0") {
          console.log("se guardo correctamente");
        }else{
          console.log("No se guardo correctamente");
        }
        this.setState({ loadingState: false });
      })

      .catch(e => console.log("ERROR"));

    this.setState({ personas: arrPersons });
  }

  deletePerson = (person, stateEdit) => {



    MySwal.fire({
      title: 'Deseas Eliminar el usuario " ' + person.nombre,
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
    }).then((result) => {
      if (result.isConfirmed) {
        var arrPersonas = this.state.personas;
        let id = person.id;
        console.log(id);
        const userFinded = arrPersonas.findIndex(ref => ref.id === id);



        console.log(userFinded);

        arrPersonas.splice(userFinded, 1);
        this.setState({ personas: arrPersonas })
        Swal.fire(
          'ELIMINADO!',
          '',
          'success'
        )
      }
    })




  }

  editPerson = (dataPerson, dataActualizada) => {
    var arrPersons = this.state.personas;
    this.setState({ editshow: true,
    dataEdit: dataPerson
    });
    let id = dataPerson.id;
    console.log(dataPerson);
    arrPersons.map( ref => {
      if(ref.id == id) {
          ref.nombre = dataPerson.nombre;
          ref.edad = dataPerson.edad;
          ref.imagen = dataPerson.imagen;
      }
    })
    this.setState({ personas: arrPersons});
  }

  render() {

    /*var listNames = personas.map((persona) =>
      <Cajas name={persona.nombre} edad={persona.edad}></Cajas>
    );*/


    var listNames = this.state.personas.map((persona, index) =>
      <Cajas key={index} persona={persona} ridPerson={this.deletePerson} modifyPerson={this.editPerson}></Cajas>
    );



    // var addNames = personas.map((persona) =>
    //   //<Add personas={persona}></Add>
    // );

    /*var sendCharge = personas.map((persona) =>
      <Boton persona={persona}></Boton>
    );*/


    return (
      <div className="App">


        <BrowserRouter>
          <Switch>
            <Route exact path="/" component={Header1}></Route>
            <Route exact path="/header2" component={Header2}></Route>
          </Switch>
        </BrowserRouter>
        <div style={{marginTop: "10px"}}>
          <button onClick={this.changeModalState} className="btn btn-primary">Agregar</button>



        </div>
        {listNames}

        <ModalAddPerson show={this.state.show} hidden={this.closeModalState} newPerson={this.addPerson} ></ModalAddPerson>
        {this.state.editshow &&
          <EditPerson show={this.state.editshow} hidden={this.closeEditModal} data={this.state.dataEdit} dataActualizada={this.editPerson}></EditPerson>
        }

        {this.state.loadingState &&
          <div class="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
        }



        {/* <img src={administrador} alt="" /> */}
        {/* {sendCharge} */}


      </div>
    );
  }

}

export default App;
